import HeaderBack from '../Common/HeaderBack'
import AddButton from '../Common/AddButton'
import TestPersonItem from '../Common/TestPersonItem'

export default {
  name: 'testperson',
  components: {
    myHeader: HeaderBack,
    myAddButton:AddButton,
    myTestPersonItem: TestPersonItem,
  },
  data () {
    return {
      header: {
        HBL_icon:"icon_back",
        HBC_text:"我的检测人",
        HBR_icon: " ",
      },
      addbutton: {
        H_msg: "+新增检测人",
        H_type: "person",
      },
      isEmpty:false,
      testPersonItems: [],
    }
  },
  methods: {
    editfn(index){
      // 编辑页面跳转
      console.log('编辑'+index);
      let value = this.testPersonItems[index];
      this.$store.commit('setData',{key:'testPersonObj',value: value});
      this.$router.push({path:'/mainpage/self/EditPerson'});
    },
    deletefn(index) {
      const _this = this;
      this.$vux.confirm.show({
        // content: '确定删除吗？',
        title: '确定删除吗？',
        onConfirm () {
          // 开始动画
          comTools.showLoading(_this, '删除中...');
          setTimeout((data) => {
            // 结束动画
            comTools.hideLoading(_this);
            if(true){
              _this.$vux.toast.show({
                type:'success',
                text:'删除成功...',
                width: '7.5em'
              });
              _this.testPersonItems.splice(index, 1);
            }
          }, 500)
        }
      });
    },
    ajax(){
      // 数据加载
      comTools.showLoading(this, '载入中...');
      setTimeout((data) => {
        // 数据加载结束动画
        comTools.hideLoading(this);
        this.testPersonItems = [];
        ['','',''].forEach((element,index,arr) => {
          let o = {
            id: 'testPersion',
            name: '龙龙',
            mobile: '18966688899',
            sex: '男',
            education: ['博士'],
            height: '188',
            weight: '88',
            birthday: '2000-01-01',
            iszz: '是',
            addressValue: ['北京市','北京市','朝阳区'],
            address: '天上人间',
          };
          this.testPersonItems.push(o);
        });
      }, 500)
    }
  },
  watch: {
    
  },
  created () {
    this.ajax();
  }
}



