export default {
  name: 'addbutton',
  props: ['addbutton'],
  data () {
    return {
      H_msg: this.addbutton.H_msg,
      H_type: this.addbutton.H_type,
    }
  },
  methods: {
    ajaxReq() {
      
    },
    handleH(){
      if (this.H_type === 'address') {
        // 新增地址
        this.$router.push({path:"/mainpage/self/addaddress"});
      }else if (this.H_type === 'person') {
        // 新增检测人
        this.$router.push({path: '/mainpage/self/addperson'});
      }
    }
  }
}



