export default {
  name: 'addressitem',
  props:['value'],
  data () {
    return {
      selData:{
        id: this.value.id,
        name: this.value.name,
        mobile: this.value.mobile,
        addr: this.value.addressValue.join("") + this.value.address,
      },
    }
  },
  methods: {
    
  },
  watch:{
    
  }
}



